.buntywp-exp-media-container {
	position: relative;
	margin-bottom: 20px;
	max-width: 100%;

	.image-container {
		position: relative;

		figure {
			margin: 0;
			position: relative;

			.uploaded-media {
				width: 100%;
				height: 100%;
				display: block;
				object-fit: cover;
				box-sizing: border-box;
				cursor: pointer;
			}

			video.uploaded-media {
				background-color: #000;
				width: 100%;
			}

			.exp-media-lightbox-trigger {
				opacity: 0;
				position: absolute;
				top: 5%;
				right: 5%;
				transition: opacity 0.2s ease;
				background-color: rgba(90, 90, 90, 0.2509803922);
				-webkit-backdrop-filter: blur(16px) saturate(180%);
				backdrop-filter: blur(16px) saturate(180%);
				height: 20px;
				width: 20px;
				text-align: center;
				display: flex;
				justify-content: center;
				border-radius: 4px;
				border: none;
				padding: 4px 0;
				cursor: zoom-in;
			}

			&:hover button.exp-media-lightbox-trigger {
				opacity: 1;
				transition: opacity 0.2s ease;
			}
		}
	}

	.exp-media-actions {
		display: flex;
		align-items: center;
		padding: 8px 0;

		.exp-media-like-button {
			background: transparent;
			border: none;
			padding: 6px;
			margin: 0;
			cursor: pointer;
			display: flex;
			align-items: center;
			justify-content: center;
			margin-right: 8px;
			transition: transform 0.15s ease;
			position: relative;
			border-radius: 50%;

			&:hover {
				background-color: rgba(0, 0, 0, 0.05);
				transform: scale(1.1);
			}

			&:focus {
				outline: none;
			}

			.heart-icon {
				stroke: #262626;
				transition: all 0.2s ease;
				position: relative;
				z-index: 1;
			}

			&.liked .heart-icon {
				fill: #e0245e;
				stroke: #e0245e;
			}
		}

		.exp-media-like-count {
			font-weight: 600;
			color: #262626;
			font-size: 14px;
		}

		.exp-media-lightbox {
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0, 0, 0, 0.9);
			z-index: 1000;
			display: flex;
			justify-content: center;
			align-items: center;
			opacity: 1;
			transition: opacity 0.3s ease;

			&.hide {
				opacity: 0;
				pointer-events: none;
			}

			.exp-media-item {

				&.full-width {
					position: relative;
					max-width: 90%;
					max-height: 90%;
					margin: 0 auto;

					img {
						max-width: 100%;
						max-height: 90vh;
						display: block;
						object-fit: contain;
						box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
					}
				}

				.exp-media-remove-popup {
					position: absolute;
					top: 0;
					right: -40px;
					background-color: transparent;
					border: none;
					cursor: pointer;
					padding: 10px;
					z-index: 2;

					svg {
						fill: #fff;
						width: 24px;
						height: 24px;
					}
				}
			}
		}
	}
}

@keyframes heartScale {
	0% {
		transform: scale(1);
	}

	50% {
		transform: scale(0);
	}

	100% {
		transform: scale(1);
	}
}

@keyframes heartBurst {
	0% {
		transform: scale(0);
	}

	50% {
		transform: scale(1.6);
	}

	100% {
		transform: scale(1);
	}
}

.exp-media-like-button.twitter-animation .heart-icon {
	animation: heartScale 0.3s forwards;
}

.exp-media-like-button.twitter-animation.liked .heart-icon {
	animation: heartBurst 0.4s forwards;
}

// Particle effects
.heart-particle {
	position: absolute;
	background: #e0245e;
	border-radius: 50%;
	pointer-events: none;
	z-index: 0;
	opacity: 0;
}

@keyframes particleFadeOut {
	0% {
		opacity: 0.8;
		transform: translate(0, 0) scale(1);
	}

	100% {
		opacity: 0;
		transform: translate(var(--tx), var(--ty)) scale(0);
	}
}